<template>
  <div>
    <serch @serch="serch" />
    <div class="content">
      <van-row>
        <van-col v-for="(item, index) in data" :key="index">
           <router-link :to="{ path: '/about', query: { url: item.url } }">
         <div class="list_context clearfix">
           <div class="list_img fl">
             <img class="auto-img" :src="item.cover" alt="">
           </div>
           <div class="list_txt fr">
             <p class="name">{{ item.name }}</p>
             <p class="man">{{item.author}}</p>
             <p class="time">{{item.time}}</p>
             <p class="latest">{{ item.latest}}</p>
           </div>
         </div>
          </router-link>
        </van-col>
        
       
      </van-row>
    </div>
  </div>
</template>

<script>
import "../../src/assets/scss/list.scss";
import { homeApi } from "@/api/api";
import serch from "./Module/Search.vue";
import { mapState } from "vuex";
export default {
  name: "List",
  data() {
    return {
      data: [],
      allList: [],
      page: {
        pageSize: 20,
        pageNo: 2
      }
      
    };
  },
  provide() {
    return {
      getHomeData: this.getHomeData
    };
  },
  methods: {
    serch(list) {
      this.data = [];
      this.data = list;
      
      if (this.load && this.load.clear) this.load.clear();
    },
    getHomeData() {
      homeApi().then(res => {
        let { mhlist } = res.data;
        this.data = mhlist.slice(0, 30);
      });
    }
  },
  created() {
    if (this.serchKey === "") {
      this.getHomeData();
    }
    
  },
  computed: {
    ...mapState(["serchKey"])
  },
  components: {
    serch
  }
};
</script>
