<template>
  <div class="top">
    <van-search v-model="value" placeholder="请输入搜索关键词" @search="onSearch" show-action shape="round">
      <div slot="action" @click="cancel">取消</div>
    </van-search>
  </div>
</template>

<script>
import { mhSerchApi } from "@/api/api";
import { mapActions, mapState } from "vuex";
export default {
  name: "",
  data() {
    return {
      value: "",
    };
  },
  inject: ["getHomeData"],
  methods: {
    ...mapActions(["setSerchKey"]),
    onSearch() {
      let { value: val, getHomeData } = this;
      
      if (val.trim() === "") {
        getHomeData();
        this.setSerchKey("");
        return;
      }
      this.$emit("serch", []);
     this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      mhSerchApi(val).then((res) => {
        let { list, code } = res.data;
        if (code === 0) {
          this.$toast.clear();
          this.setSerchKey(val);
          this.$emit("serch", list);
        } else {
          this.$toast.fail("服务器挂了");
        }
      });
    },
    cancel() {
      this.$router.push({
        path: "/",
      });
    },
  },
  created() {
    let { serchKey, onSearch } = this;
    if (serchKey !== "") {
      this.value = serchKey;
      onSearch();
    }
  },
  computed: {
    ...mapState(["serchKey"]),
  },
};
</script>

